<template>
  <div id="tablet">
    <div class="queryBox">
      <QueryBox :formArr="queryFrom" @query="getQueryData" style="width: calc(100% - 300px)"></QueryBox>
      <div class="queryBtns">
        <Button type="primary" size="small" class="btn-tabBtn" @click.stop="addActive=true;$refs.taskManagementTableMap.initCom()"><i class="iconfont icon-leimupinleifenleileibie"></i>切换地图</Button>
        <Button type="success" size="small" icon="md-add" class="btn-add" style="margin-right: 10px" @click.stop="openAdd('add', '')">新建</Button>
        <Button type="success" size="small" class="btn-download" style="padding: 2px 7px 1.8px;margin-right: 10px">
          <Upload
            :show-upload-list="false"
            :format="['.xls']"
            action="#">
            <i class="iconfont icon-upload"></i>
            <span>导入</span>
          </Upload>
        </Button>
        <Button type="primary" size="small" class="btn-exportBtn"><i class="iconfont icon-xiazai"></i>下发</Button>
      </div>
    </div>
    <div class="content_box">
      <div>
        <Table border stripe :loading="loading" :columns="columns1" :data="data1" highlight-row :max-height="tableHeight"></Table>
        <div class="page_box">
          <Page :total="total" ref="pages" :page-size="pageSize" :current.sync="pageNum" @on-change="pageChange" show-total/>
        </div>
      </div>
    </div>
    <!-- 查看-->
    <div class="extract_box" :class="{addActive:addActive}">
      <activitiesInspectTableMap ref="taskManagementTableMap" @signOut="signOut"></activitiesInspectTableMap>
    </div>
    <!-- 新建-->
    <Modal
      :title="modalStatus + '核查任务'"
      v-model="modal">
      <Form ref="addForm" :model="AddData" :rules="ruleValidate" :label-width="100" style="padding:0 20%;">
        <FormItem prop="num" label="问题点编号：">
          <Input v-model.trim="AddData.num" placeholder="请输入问题点编号" size="default"/>
        </FormItem>
        <FormItem prop="type" label="所属任务：">
          <Select v-model.trim="AddData.type" size="default" placeholder="请选择所属任务">
            <Option v-for="item in addTypeList" :value="item.value" :key="item.value">{{ item.label }}</Option>
          </Select>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="addSubmit">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import QueryBox from '@/components/publicQuery'
import activitiesInspectTableMap from './taskManagementTable_map'
import { getTaskList } from '@/api/taskManager'

export default {
  name: 'extracTable',
  data () {
    return {
      /* 表格高度 */
      tableHeight: Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40,
      /* 查询框查询条件 */
      queryData: {},
      typeList: [
        {
          value: '',
          label: '请选择'
        },
        {
          value: '1',
          label: 'type1'
        },
        {
          value: '2',
          label: 'type2'
        }
      ],
      /* 表格数据 */
      columns1: [
        {
          type: 'selection',
          fixed: 'left',
          width: 60,
          align: 'center'
        },
        {
          title: '序号',
          fixed: 'left',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + (this.pageNum - 1) * this.pageSize + 1)
          }
        },
        {
          title: '问题点编号',
          fixed: 'left',
          align: 'center',
          minWidth: 200,
          key: 'srld_code'
        },
        {
          title: '所属任务',
          align: 'center',
          minWidth: 200,
          key: 'srld_code'
        },
        {
          title: '经度',
          align: 'center',
          minWidth: 100,
          key: 'srld_code'
        },
        {
          title: '纬度',
          align: 'center',
          minWidth: 100,
          key: 'srld_code'
        },
        {
          title: '问题点类型',
          align: 'center',
          minWidth: 150,
          key: 'srld_code'
        },
        {
          title: '问题点备注',
          align: 'center',
          minWidth: 200,
          key: 'srld_code'
        },
        {
          title: '添加人',
          align: 'center',
          minWidth: 100,
          key: 'srld_code'
        },
        {
          title: '操作',
          key: 'address',
          width: 320,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              // 查看地图
              h('Button',
                {
                  props: {
                    type: 'success',
                    size: 'small',
                    class: 'btn-add'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.addActive = true
                      this.$refs.taskManagementTableMap.initCom()
                    }
                  }
                },
                [h('i', {
                  class: 'iconfont icon-leimupinleifenleileibie',
                  style: {
                    fontSize: '12px',
                    marginRight: '4px'
                  }
                }),
                h('span', {
                  style: {
                    color: 'inherit',
                    cursor: 'pointer'
                  }
                }, '地图查看')]
              ),
              // 审核
              h('Button',
                {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      console.log('审核')
                    }
                  }
                },
                [h('i', {
                  class: 'iconfont icon-shenhe',
                  style: {
                    fontSize: '12px',
                    marginRight: '4px'
                  }
                }),
                h('span', {
                  style: {
                    color: 'inherit',
                    cursor: 'pointer'
                  }
                }, '审核')]
              ),
              // 修改
              h('Button', {
                props: {
                  type: 'warning',
                  size: 'small',
                  icon: 'md-create'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.openAdd('modify', params.row)
                  }
                }
              }, '修改'),
              // 删除
              h('Button', {
                props: {
                  type: 'error',
                  size: 'small',
                  icon: 'md-trash'
                },
                on: {
                  click: () => {
                    this.$Modal.confirm({
                      title: '确认',
                      content: '请问您确定要删除该条数据吗？',
                      onOk: () => {
                        this.infoDel(params.row.id)
                      },
                      onCancel: () => {
                      }
                    })
                  }
                }
              }, '删除')
            ])
          }
        }
      ],
      data1: [],
      /* 总条数 */
      total: 0,
      pageNum: 1,
      pageSize: Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48),
      /* 加载 */
      loading: false,
      /* 提取信息框状态 */
      addActive: false,
      // 添加
      modal: false, // 添加的弹框
      modalStatus: '',
      /* 添加的弹框-表单参数 */
      AddData: {
        num: '',
        type: ''
      },
      /* 添加的弹框-表单校验 */
      ruleValidate: {
        num: [
          { required: true, message: '问题点编号必须填写', trigger: 'blur' }
        ],
        type: [
          { required: true, message: '所属任务必须选择', trigger: 'change' }
        ]
      },
      /* 添加的弹框-类型参数 */
      addTypeList: [
        {
          value: '1',
          label: 'type1'
        },
        {
          value: '2',
          label: 'type2'
        }
      ]
    }
  },
  components: {
    QueryBox,
    // 人类活动核查信息地图
    activitiesInspectTableMap
  },
  computed: {
    // 查询表单
    queryFrom: function () {
      return [
        {
          type: 1,
          field: 'srld_code',
          title: '问题点编号'
        },
        {
          type: 2,
          field: 'address',
          title: '所属任务',
          data: this.typeList
        }
      ]
    }
  },
  created () {
    this.getTableList()
  },
  watch: {
    queryData () {
      this.getTableList()
    }
  },
  mounted () {
    // 窗口改变时table自适应
    window.addEventListener('resize', () => {
      this.tableHeight = Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40
      this.pageSize = Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48)
      this.getTableList()
    }, false)
  },
  methods: {
    /* 获取查询数据 */
    getQueryData (data) {
      this.queryData = data
      this.pageNum = 1
      this.getTableList()
    },
    /* 获取字段列表 */
    getTableList () {
      this.loading = true
      getTaskList({
        data: {
          pageSize: this.pageSize,
          pageNumber: this.pageNum,
          ...this.queryData
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.data1 = res.data.data.rows
          this.total = res.data.data.total
          this.loading = false
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 分页跳转 */
    pageChange (pageNum) {
      this.pageNum = pageNum
      this.getTableList()
    },
    /* axios: 删除数据 */
    infoDel (e) {
      infoDel({
        data: {
          id: e
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.getTableList()
          this.$Notice.success({ title: '删除成功' })
        } else {
          this.$Notice.error({ title: '删除失败' })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 信息提取地图页面退出 */
    signOut (addActive) {
      this.addActive = addActive
    },
    /* 添加核查任务 */
    addSubmit () {
      this.$refs.addForm.validate(valid => {
        if (valid) {
          saveFiled({
            data: {
              num: this.AddData.name,
              type: this.AddData.type
            }
          }).then(res => {
            if (res.data.code === '0000') {
              this.$Notice.success({ title: this.modalStatus + '核查任务成功' })
              this.modal = false
              this.getTableList()
            } else {
              this.$Notice.error({ title: this.modalStatus + '核查任务失败' })
            }
          })
        }
      })
    },
    /* 打开弹框 */
    openAdd (status, row) {
      this.modal = true
      // 清空表单校验
      this.$refs.addForm.resetFields()
      if (status === 'add') {
        this.$nextTick(() => {
          this.modalStatus = '新增'
        })
      } else if (status === 'modify') {
        // 把列表中单条数据赋给修改模态框
        this.AddData = row
        this.$nextTick(() => {
          this.modalStatus = '修改'
        })
      }
    }
  }
}
</script>

<style lang='scss' scoped rel='stylesheet/scss'>
  #tablet{
    position: relative;
    .ml20 {
      margin-left: 20px;
    }
    .mr20 {
      margin-right: 20px;
    }
    .mt15 {
      margin-top: 15px;
    }
    .pull-right {
      float: right;
    }
    .btn-tabBtn {
      background: rgb(16,186,204);
      border-color: rgb(16,186,204);
      margin-right: 10px;
      &:hover {
        background: #13B1CC;
        border-color: #13B1CC;
      }
    }
    .iconfont {
      margin-right: 4px;
      &.icon-xuanzewenjian {
        font-size: 12px;
      }
      &.icon-xiazai {
        font-size: 13px;
      }
      &.icon-upload {
        font-size: 12px;
      }
      &.icon-leimupinleifenleileibie {
        font-size: 12px;
      }
    }
    .queryBox {
      height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px 15px;
      position: relative;
      overflow: inherit;
      .queryBtns {
        float: right;
        margin-top: 14px;
      }
    }
    .content_box {
      margin: 0 15px 0;
      .page_box{
        width: 100%;
        padding: 15px;
        text-align: right
      }
    }
    .extract_box{
      height: 100%;
      width: 100%;
      position: absolute;
      right: -120%;
      top:0;
      z-index: 1356;
      background: #fff;
      transition: all 0.8s ease;
      border-radius: 0 0 5px 5px;
      .closeBtn{
        position: absolute;
        right:10px;
        bottom:10px;
        z-index: 131531313;
      }
    }
    .addActive{
      right:0;
    }
    .btn-add {
      background-color: #01c296;
      border-color: #01c296;
      &:hover, &:focus {
        background-color: #05ab85;
        border-color: #05ab85;
      }
    }
    .btn-exportBtn {
      background: #2d579b;
      border-color: #2d579b;
      &:hover {
        background: #1a3a6f;
        border-color: #1a3a6f;
      }
    }
  }
</style>
